import type { FC } from 'react';
import { useState } from 'react';
import PerfectScrollbar from 'react-perfect-scrollbar';
import { Chip, Box, Button, } from '@material-ui/core';
import PlusCircle from 'src/icons/PlusCircle';

export const Filters:FC = () => {
  const [filters, setFilter] = useState([
    {
      selected: true,
      label: 'Status',
      value: 'STATUS'
    },
    {
      selected: false,
      label: 'Location',
      value: 'LOCATION'
    },
    {
      selected: false,
      label: 'Protocol',
      value: 'PROTOCOL'
    },
    {
      selected: false,
      label: '# of Allergen',
      value: 'OF_ALLERGEN'
    },
  ]);

  const handleChangeFilters = (index:number) => {
    filters.forEach((item) => (item.selected = false));
    filters[index].selected = true;
    setFilter([...filters]);
  };

  return (
    <Box sx={{
      display: 'flex',
      justifyContent: 'space-between',
      px: 2,
      my: 2,
      mt: 2.5
    }}
    >
      <PerfectScrollbar>
        <Box sx={{ display: 'flex',
          flexWrap: 'nowrap',

        }}
        >
          {filters.map((item, index:number) => (

            <Chip
              key={`all-patients-${item.value}`}
              label={`${item.label}`}
              variant="outlined"
              onClick={() => {
                handleChangeFilters(index);
              }}
              sx={{
                borderColor: '#C7C9C7',
                color: '#C7C9C7',
                mr: 1,
                ...(
                  item.selected && {
                    borderColor: '#373737',
                    color: '#373737'
                  }
                )
              }}
            />

          ))}
        </Box>
      </PerfectScrollbar>
      <Box>
        <Button
          startIcon={(
            <PlusCircle sx={{
              mt: 1,
              fontSize: 40,
            }}
            />
)}
          variant="contained"
          sx={{
            borderRadius: '8px'
          }}
        >
          Add New Patient
        </Button>
      </Box>

    </Box>
  );
};
