/* eslint-disable @typescript-eslint/no-unused-vars */
import { useEffect } from 'react';
import type { FC } from 'react';
import { Link as RouterLink, useLocation } from 'react-router-dom';
import { Box, Drawer, Divider } from '@material-ui/core';
import PropTypes from 'prop-types';
import type { Theme } from '@material-ui/core';
import useMediaQuery from '@material-ui/core/useMediaQuery';

import Logo from '../Logo';
import NavSection from '../NavItem';
import Scrollbar from '../Scrollbar';
import PulseIcon from '../../icons/Fire';
import Bell from 'src/icons/Gallery';

interface DashboardSidebarProps {
  onMobileClose: () => void;
  openMobile: boolean;
}

const sections = [
  {
    title: 'General',
    items: [
      {
        title: 'Overview',
        path: '/dashboard',
        icon: <PulseIcon fontSize="small" />
      },
      {
        title: 'Analytics',
        path: '/dashboard/analytics',
        icon: <Bell fontSize="small" />
      },
    ]
  },
];

const DashboardSidebar: FC<DashboardSidebarProps> = (props) => {
  const { onMobileClose, openMobile } = props;
  const location = useLocation();
  const lgUp = useMediaQuery((theme: Theme) => theme.breakpoints.up('lg'));

  useEffect(() => {
    if (openMobile && onMobileClose) {
      onMobileClose();
    }
  }, [location.pathname]);

  const content = (
    <Box
      sx={{
        display: 'flex',
        flexDirection: 'column',
        height: '100%'
      }}
    >
      <Scrollbar options={{ suppressScrollX: true }}>
        <Box
          sx={{
            display: {
              xs: 'flex'
            },
            paddingTop: {
              xs: '16px'
            },
            justifyContent: 'center',
          }}
        >
          <RouterLink
            to="/"
          >
            <Logo
              sx={{
                height: 30,
                width: 30,
                mt: 0.5,
                mb: 1
              }}
            />
          </RouterLink>
        </Box>
        <Box sx={{ paddingInline: 10 }}>
          <Divider />
        </Box>
        <Box>
          {sections.map((section) => (
            <NavSection
              key={section.title}
              // @ts-ignore
              pathname={location.pathname}
              sx={{
                '& + &': {
                  mt: 3
                }
              }}
              {...section}
            />
          ))}
        </Box>
      </Scrollbar>
    </Box>
  );

  if (lgUp) {
    return (
      <Drawer
        anchor="left"
        open
        PaperProps={{
          sx: {
            backgroundColor: 'background.paper',
            height: 'calc(100%) !important',
            // top: '64px !Important',
            width: '100px'
          }
        }}
        variant="permanent"
      >
        {content}
      </Drawer>
    );
  }

  return (
    <Drawer
      anchor="left"
      onClose={onMobileClose}
      open={openMobile}
      PaperProps={{
        sx: {
          backgroundColor: 'background.paper',
          width: '100px'
        }
      }}
      variant="temporary"
    >
      {content}
    </Drawer>
  );
};

DashboardSidebar.propTypes = {
  onMobileClose: PropTypes.func.isRequired,
  openMobile: PropTypes.bool.isRequired
};

export default DashboardSidebar;
