import { FC } from 'react';
import { Grid, Box, Card, Typography } from '@material-ui/core';
import PropTypes from 'prop-types';

interface Props {
  title:string;
  subTitle:string;
  Icon:any
}

export const AnalyticsCard: FC<Props> = ({ title, subTitle, Icon }) => (
  <Grid
    item
    md={3}
    sm={6}
    xs={12}
  >
    <Card sx={{
      borderRadius: '8px'
    }}
    >
      <Box
        sx={{
          alignItems: 'flex-start',
          display: 'flex',
          p: 1
        }}
      >
        <Box sx={{ mr: 4 }}>
          { Icon }
        </Box>
        <Box>
          <Typography
            color="textPrimary"
            variant="h4"

          >
            {title}
          </Typography>
          <Typography
            color="textPrimary"
            sx={{ mt: 1 }}
            variant="subtitle2"
          >
            {subTitle}
          </Typography>
        </Box>
      </Box>
    </Card>
  </Grid>

);

AnalyticsCard.propTypes = {
  title: PropTypes.string.isRequired,
  subTitle: PropTypes.string.isRequired,
  Icon: PropTypes.any.isRequired
};
